import { useState, useEffect } from 'react';
import { useSelector, useDispatch } from "react-redux";
import { Link, useLocation } from 'react-router-dom';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import { addExercise, updateExercise, deleteFile } from 'redux/exercises/operations';
import { selectExercise } from 'redux/exercises/selectors';
import { BASE_CLIENT_URL } from '../../constants';
import css from './HomeworkForm.module.css';

export const HomeworkForm = ({courseId, lessonId}) => {
  const dispatch = useDispatch(); 
  const location = useLocation();
  const currentURL = location.pathname; 

  const {homework, fileURL} = useSelector(selectExercise);
  const [textInput, setTextInput] = useState(homework);
  const [fileInput, setFileInput] = useState(null);
  const [isActiveTextarea, setIsActiveTextarea] = useState(false);

  const handleTextChange = (e) => {
    setTextInput(e.target.value);
    setIsActiveTextarea(true);
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    setFileInput(file);
  };

  const isTextValid = (text) => {
    if (text === '' || text.length > 3000) {
      alert('–ü–æ–ª–µ –ø–æ–≤–∏–Ω–Ω–æ –±—É—Ç–∏ –∑–∞–ø–æ–≤–Ω–µ–Ω–∏–º —ñ –º—ñ—Å—Ç–∏—Ç–∏ –Ω–µ –±—ñ–ª—å—à–µ 3000 —Å–∏–º–≤–æ–ª—ñ–≤.');
      return false;
    }
    return true;
  };

  const isFileValid = (file) => {
    if (file.size > 1048576) {
      alert('–§–∞–π–ª –ø–æ–≤–∏–Ω–µ–Ω –±—É—Ç–∏ –Ω–µ –±—ñ–ª—å—à–µ 1 –ú–±.');
      return false;
    }
    return true;
  };

  const handleDeleteFile = () => {
    if (fileURL === '') {
      return;
    };

    const formData = {
      courseId,
      lessonId,
      fileURL,
    };

    dispatch(
      deleteFile(formData)
    );
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    
    const formData = new FormData();
    formData.append('courseId', courseId);
    formData.append('lessonId', lessonId);

    if (!isTextValid(textInput)) {
      return;
    }

    formData.append('homework', textInput);

    if (fileInput) {
      
        if (!isFileValid(fileInput)) {
          return;
        }

        formData.append('file', fileInput);
    }
  
    if (homework === '') {
      dispatch(
        addExercise(formData)
      );
    } else {
      dispatch(
        updateExercise(formData)
      );
    }
    setIsActiveTextarea(false);
  };

  const shareHomework = () => {
    if (homework === '' || isActiveTextarea) {
      alert('–°–ø–æ—á–∞—Ç–∫—É –∑–±–µ—Ä–µ–∂—ñ—Ç—å –¥–æ–º–∞—à–Ω—é —Ä–æ–±–æ—Ç—É.');
      return;
    }

    if (fileURL && fileURL !== '') {
      window.open(`https://t.me/share/url?url=${fileURL}&text=${encodeURIComponent(homework)}`);
    } else {
      window.open(`https://t.me/share/url?url=${BASE_CLIENT_URL}${currentURL}&text=${encodeURIComponent(homework)}`);
    }
  };

  useEffect(() => {
    // –§—É–Ω–∫—Ü—ñ—è-–æ–±—Ä–æ–±–Ω–∏–∫ –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –ø–æ–¥—ñ—ó beforeunload
    const handleBeforeUnload = (e) => {
      // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞, —á–∏ –∞–∫—Ç–∏–≤–Ω–µ —Ç–µ–∫—Å—Ç–æ–≤–µ –ø–æ–ª–µ, —ñ —è–∫—â–æ —Ç–∞–∫, –ø–æ–ø–µ—Ä–µ–¥–∂–µ–Ω–Ω—è –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞
      if (isActiveTextarea) {
        e.preventDefault();
        e.returnValue = '';
      }
    };

    // –î–æ–¥–∞–≤–∞–Ω–Ω—è –æ–±—Ä–æ–±–Ω–∏–∫–∞ –ø–æ–¥—ñ—ó beforeunload –ø—Ä–∏ –º–æ–Ω—Ç—É–≤–∞–Ω–Ω—ñ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    window.addEventListener('beforeunload', handleBeforeUnload);

    // –í–∏–¥–∞–ª–µ–Ω–Ω—è –æ–±—Ä–æ–±–Ω–∏–∫–∞ –ø–æ–¥—ñ—ó beforeunload –ø—Ä–∏ —Ä–æ–∑–º–æ–Ω—Ç—É–≤–∞–Ω–Ω—ñ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, [isActiveTextarea]);

  return (
    <>
      <Form onSubmit={handleSubmit} className={css.form}>
        <Form.Group 
          controlId="formText"
          className={css.groupTextarea} 
        >
          <Form.Label className={css.label}>
            –î–æ–º–∞—à–Ω—è —Ä–æ–±–æ—Ç–∞
          </Form.Label>
          <Form.Control 
            as="textarea" rows={10} 
            placeholder="–í–≤–µ–¥—ñ—Ç—å —Ç–µ–∫—Å—Ç" 
            value={textInput} 
            onChange={handleTextChange}
            className={css.textarea} 
          />
        </Form.Group>
        {fileURL && fileURL !== '' ?
          <div className={css.groupFile}>
            <Link
              to={fileURL}
              target='blank'
              className={css.link}         
            >
              –ü—Ä–∏–∫—Ä—ñ–ø–ª–µ–Ω–∏–π —Ñ–∞–π–ª
            </Link>
            <Button 
              variant="danger"
              type='button' 
              onClick={handleDeleteFile}
              className={css.dangerBtn}
            >
              –í–∏–¥–∞–ª–∏—Ç–∏ —Ñ–∞–π–ª
            </Button>
          </div>
          :
          <Form.Group 
            controlId="formFile" 
            className={css.groupFile}
          >
            <Form.Control 
              type="file" 
              onChange={handleFileChange}
              className={css.file} 
            />               
          </Form.Group>
        }
        <div className={css.wrapperBtn}>
          <Button
            onClick={shareHomework} 
            className={css.shareBtn}
          >
            –ü–æ–¥—ñ–ª–∏—Ç–∏—Å—è
          </Button> 
          <Button 
            variant="primary"
            type="submit"
            className={css.primaryBtn}
          >
            –ó–±–µ—Ä–µ–≥—Ç–∏
          </Button>
        </div>        
      </Form>
    </>
  ) 
};

